import Jumbo from '../header/jumbo'
import React from 'react'
import Card from 'react-bootstrap/Card'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'
import './style.css'
const Functional = () => {
  return (
    <section>
     <h2 id='fh'>Functional Component</h2>
     <Col id='cardz'>
      <Card bg='primary' text='white' className='text-center '>
        <blockquote className='blockquote mb-0 card-body'>
          <p>
            React allows for two ways to construct a component. One is by
            creating a Class Component or by creating a ... (yup you guessed it)
            ... Functional Component
          </p>
          <footer className='blockquote-footer'>
            <small className='text-muted'>
              Someone famous in <cite title='Source Title'>Source Title</cite>
            </small>
          </footer>
        </blockquote>
      </Card>
      </Col>

      <Container id='fc'>
        <Row>
          <Col>
            <Card>
              <Card.Body>
                <Card.Title>Functional Component</Card.Title>
                <Card.Subtitle className='mb-2 text-muted'>
                  Star On The Rise
                </Card.Subtitle>
                <Card.Text>
                  Some quick example text to build on the card title and make up
                  the bulk of the card's content.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col id='vs'>
            <Card id='vsc'>Vs</Card>
          </Col>
          <Col>
            <Card>
              <Card.Body>
                <Card.Title>Class Component</Card.Title>
                <Card.Subtitle className='mb-2 text-muted'>
                  Oldie But Goodie
                </Card.Subtitle>
                <Card.Text>
                  <ul>
                    <li>A JS class that extends React.Component</li>
                    <li>Utilizes the render()</li>

                    <li>componentDidMount()</li>
                  </ul>
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default Functional;



