import Jumbotron from "react-bootstrap/Jumbotron"
import Container from "react-bootstrap/Container";
import React from 'react'
const Jumbo = (props)=> {
    return (
        <section>
        <Jumbotron fluid id="h-c">
        <Container>
          <h1>{props.JHeader}</h1>
          <p>
            {props.JContent}
          </p>
        </Container>
      </Jumbotron>
        </section>
    )

    
}
export default Jumbo


