import React from 'react'
import Card from 'react-bootstrap/Card'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Container from 'react-bootstrap/Container'
import './style.css'
const Axios = () => {
  return (
    <section>
      <Card bg='success' text='white' className='text-center p-3'>
        <h3>Axios</h3>
        <blockquote className='blockquote mb-0 card-body'>
          <p>There are always at least two ways. But Axios is the best way.</p>
        </blockquote>
      </Card>
      <Container id='awc'>
        <Row>
          <Col sm={8}>
            <Card  id='img'>
              <Card.Img
                variant='top'
                src='https://cdn2.thecatapi.com/images/at0.jpg'
              />
              <Card.Body>
                <Card.Text>
                 API2 Data will render here
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col sm={4}>
            <Card bg='primary'>
              <Card.Body id='ac'>
                <Col><ul>
                <li>Designed to handle http requests and responses</li>
                <li>Provides grand amount of variety features </li>
                <li>Utilizes get() and post()</li>
              </ul></Col>
              
              </Card.Body>
            </Card>
            <Row>
              <Col>
              Testing Placement</Col></Row>
          </Col>
        </Row>
      </Container>
     
    </section>
  )
}

export default Axios;



