import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
// import Jumbo from '../header/jumbo.js'
import Container from "react-bootstrap/Container";
import Card from "react-bootstrap/Card"
import "./style.css"
import React from 'react'
const LifeCycle = (props) => {
  return (
    <section >
      <Card bg="primary" text="white" className="text-center p-3">
        <blockquote className="blockquote mb-0 card-body">
          <h1>Life Cycles</h1>
          <footer className="blockquote-footer">
            <medium className="text-muted">
              The Three Stages Of A Component
            </medium>
          </footer>
        </blockquote>
      </Card>

      <Container>
        <Row id="lcc">
          <Col>
            <Row>
              <Card
                bg={"info"}
                text={"white"}
                style={{ width: "18rem" }}
                className="mb-2"
              >
                <Card.Header>Mounting</Card.Header>
                <Card.Body>
                  <Card.Text>
                    When a component is in the mounting stage - the data (or props) are being prepared to render onto the clients DOM. 
                  </Card.Text>
                </Card.Body>
                <Card.Footer><button onClick={props.mount}>Learn More</button></Card.Footer>
              </Card>
            </Row>
          </Col>
          <Col>
            <Card
              bg={"warning"}
              text={"white"}
              style={{ width: "18rem" }}
              className="mb-2"
            >
              <Card.Header>Updating</Card.Header>
              <Card.Body>
                <Card.Text>
                  Once a component has rendered and a new state or prop is provided by the client or server; the update stage is invoked.
                </Card.Text>
              </Card.Body>
              <Card.Footer><button onClick={props.update}>Learn More</button></Card.Footer>
            </Card>
          </Col>
          <Col>
            <Card
              bg={"success"}
              text={"white"}
              style={{ width: "18rem" }}
              className="mb-2"
            >
              <Card.Header>Unmounting</Card.Header>
              <Card.Body>
                <Card.Text>
                  Finally, once a component is in the process of being removed or hidden from the DOM. The unmounting lifecycle begins
                </Card.Text>
              </Card.Body>
              <Card.Footer><button onClick={props.unmount}>Learn More</button></Card.Footer>
            </Card>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default LifeCycle;



